import React from "react";
import { Redirect } from "react-router";
import { useApp } from "../../context/AppContext";
import { getDayInfoWithDate } from "../../datas/delay";
import { getRegulWithDate } from "../../datas/reguls";
/**
 * Stateless component for ACDS Notification.
 *
 * @name Date
 * @author Bruno Boissie <bruno.boissie@aviation-civile.gouv.fr>
 * @copyright (c) 2020, DSNA/DTI. All rights reserved.
 *
 * @param {Object}	props		Incoming component properties
 * @return	{Node}		Rendered Component node
 */
const InfoHistory = props => {

	const [{ page, user, histodate }, dispatch] = useApp();

	const sect = "Secteurs ouverts";

	
	let delay;

	const delayday = getDayInfoWithDate(histodate);

	const getNbRegulWithDate=(d)=>{
		const regulday = getRegulWithDate(d);
		//console.log("[Reguls] ="+JSON.stringify(regulday));
		
		return regulday.length === 0 ? "unknown" : regulday[0].value.length;
	}

	const nbRegul=getNbRegulWithDate(histodate);

	//console.log("Delay=" + JSON.stringify(delayday));
	//console.log("Histodate=" + histodate);



	const navTo = page => {
		dispatch({
			type: "navTo",
			payload: {
				page
			}
		});
	};

	const isHisto = () => (page === "history" || page === "/history");
	const navToPages = () => (user.email !== undefined && !isHisto());

	return (
		navToPages()
			? <Redirect to={page} />
			: (
				<>
					<div className="infohistory" id="infohistory">
						{sect}
						<br />
						
							 Délais du jour
							 { (delayday.length !== 0) && (
									<div className="delaydetails">
										Delay (av) :
										{delayday[0].value.delayAv}
										{" "}
										min
										<br />
										Delay (tot) :
										{delayday[0].value.delayTot}
										{" "}
										min
										<br />
										Regulation:
										{nbRegul}
										{" "}
										Deviation:
										{delayday[0].value.deviation}
										{" "}
										sec
									</div>

								)}

						
					</div>
				</>
			)
	);
};

export default InfoHistory;