import * as d3 from 'd3';
const posX=135;
class D3MoletteComponent {

  containerEl;
  props;
  svg;
  constructor(containerEl, props) {
    this.containerEl = containerEl;
    this.props = props;
    this.angle = 0;
    this.vitesse = 0;
    this.rotation = "";
    this.startdrag = null;
    this.drag = null;
    this.bouton = "middle";
    this.interval = null;
    this.interval2 = null;
    console.log("[D3MoletteComponent] Creation molette avec props=" + JSON.stringify(props));

    this.svg = d3.select(containerEl)
      .append("svg")
      .attr("id", "molettesvg");
    this.createMolette();
  }


  setVitesse = (v) => this.vitesse = v;
  setRotation = (r) => this.rotation = r;
  setStartDrag = (d) => {
    this.startdrag = d;
  };

  // Methode appelée lors du drag
  draghandler = () => {
    //console.log("Drag on component");
    if (this.drag === null || this.startDrag === null) return;
    //console.log("StartDrag is " + JSON.stringify(this.startDrag) + " and Drag is " + JSON.stringify(this.drag));
    let a = Math.atan2((this.drag[1] - 100), (this.drag[0] - posX));
    let ma = a > 0 ? a : (2 * Math.PI + a);
    this.setVitesse(1);
    if (ma > this.angle) this.setRotation("indirecte")
    else this.setRotation("directe");
    let newangle = ma * 180 / Math.PI;
    console.log("[D3MoletteComponent] Nouvel angle=" + newangle);
    this.updateMolette(newangle);
  };
  // handler pour les boutons
  boutonhandler = () => {
    console.log("[D3MoletteComponent] Bouton vaut maintenant " + this.bouton);
    if (this.bouton !== "middle") {
      this.interval = setInterval(() => {
        if (this.vitesse < 9) this.vitesse++;
      }, 500);
    }
    if (this.bouton === "right") this.setRotation("directe");
    else if (this.bouton === "left") this.setRotation("indirecte");
    else {
      console.log("[D3MoletteComponent] Clear interval");
      clearInterval(this.interval);
      this.setRotation("");
      this.vitesse = 0;
    }
    this.rotationhandler();
  }
  // gestion de la rotation
  rotationhandler = () => {
    console.log("[D3MoletteComponent] Rotation change " + this.rotation);
    if (this.rotation !== "") {
      console.log("[D3MoletteComponent] bouton est " + this.bouton);
      if (this.bouton !== "middle" && this.interval2 === null) {
        this.interval2 = setInterval(() => {
          let newangle = this.angle + (this.rotation === "indirecte" ? this.vitesse * 6 : -this.vitesse * 6);
          console.log("[D3MoletteComponent] rotation sens " + this.rotation + " vitesse=" + this.vitesse + " nouvel angle=" + this.angle);
          this.updateMolette(newangle);
        }, 500);
      }
    }
    else if (this.bouton === "middle") {
      console.log("[D3MoletteComponent] Clear interval");
      clearInterval(this.interval2);
      this.interval2 = null;
    }
  }
  setBouton = (d) => {
    this.bouton = d;
    this.boutonhandler();
  }
  onButton = (d) => d === "left" ? this.setBouton("left") : this.setBouton("right");
  onEndButton = (d) => this.setBouton("middle");
  setDrag = (d) => {
    this.drag = d;
    this.draghandler();
  }
  // Dessin d'une portion de la "molette" entre startAngle et endAngle
  drawSecteurOn = (gNode, cx, cy, r1, r2, startAngle, endAngle) => {
    let arcGen = d3.arc()
      .innerRadius(r1)
      .outerRadius(r2)
      .startAngle(startAngle)
      .endAngle(endAngle);

    gNode.append("path")
      .attr("d", arcGen)
      .attr("class", "arcPie")
      .attr("transform", "translate(" + cx + "," + cy + ")")
  };

  // Dessine un pourtour transparent pour recuperer les evenements souris
  drawCircularControlOn = (gNode, cx, cy, r1, r2, displaytype) => {
    let arcGen = d3.arc()
      .innerRadius(r1)
      .outerRadius(r2)
      .startAngle(0)
      .endAngle(2 * Math.PI);
    let mouse = () => displaytype === "mobile" ? d3.touches(gNode.node())[0] : d3.mouse(gNode.node());
    let isDown = false;
    const arcPieControl = gNode.append("path")
      .attr("d", arcGen)
      .attr("class", "arcPieControl")
      .attr("transform", "translate(" + cx + "," + cy + ")")
      .on(displaytype === "mobile" ? "touchstart" : "mousedown", (d) => {
        this.setStartDrag(mouse());
        arcPieControl.classed('startdrag', true);
        isDown = true;
        //console.log("[D3MoletteComponent] bouton appuyé "+isDown);
      }
      )
      .on(displaytype === "mobile" ? "touchmove" : "mousemove", (d) => {
        //console.log("[D3MoletteComponent] bouton appuyé "+isDown);
        if (isDown) {
          this.setDrag(mouse());
          arcPieControl.classed('drag', true);
          /* if (displaytype === "mobile") {
             const item = d.changedTouches.item(0);
             if (gNode === null || item === null) return false;
             let isOn = gNode.getBoundingClientRect().right > item.clientX &&
               gNode.getBoundingClientRect().left < item.clientX &&
               gNode.getBoundingClientRect().top < item.clientY &&
               gNode.getBoundingClientRect().bottom > item.clientY;
             if (!isOn) {
               this.setDrag(null);
               this.setStartDrag(null);
               arcPieControl.classed('startdrag', false);
               arcPieControl.classed('drag', false);
             }
 
           }
           */
        }
      })
      .on(displaytype === "mobile" ? "touchend" : "mouseup", (d) => {
        isDown = false;
        console.log("[D3MoletteComponent]bouton appuyé " + isDown);
        this.setDrag(null);
        this.setStartDrag(null);
        arcPieControl.classed('startdrag', false);
        arcPieControl.classed('drag', false);

      })
    // mouse out sur desktop
    if (displaytype === "desktop") {
      arcPieControl.on("mouseout", (d) => {
        isDown = false;
        console.log("[D3MoletteComponent]bouton appuyé " + isDown);
        this.setDrag(null);
        this.setStartDrag(null);
        arcPieControl.classed('startdrag', false);
        arcPieControl.classed('drag', false);
      })
    }


  }
  // Dessin de la molette a un angle d'inclinaison donné
  drawMoletteWithAngleOn = (gNode, cx, cy, r1, r2, nRay) => {
    gNode.selectAll("g").remove();
    const gNodeRot = gNode.append("g").attr("id", "gMoletteRot");
    for (let i = 0; i < nRay; i++) {
      this.drawSecteurOn(gNodeRot, cx, cy, r1, r2, i * 2 * Math.PI / nRay, (i + 1) * 2 * Math.PI / nRay);
    }
  };

  // Dessin du bouton central (a rendre paramétrable aussi)
  drawCenterPanelControlOn = (gNode) => {
    gNode
      .append("line")
      .attr("x1", posX)
      .attr("y1", 80)
      .attr("x2", posX)
      .attr("y2", 120)
      .attr("class", "rline")

    let path1 = d3.path();
    path1.moveTo(posX-5, 80);
    path1.lineTo(posX-5, 120);
    path1.lineTo(posX-15, 100);
    path1.lineTo(posX-5, 80);

    gNode.append("path")
      .attr("d", path1)
      .attr("class", "rline")
      .on("mousedown", (d) => this.onButton("left"))
      .on("mouseup", (d) => this.onEndButton())


    let path2 = d3.path();
    path2.moveTo(posX+5, 80);
    path2.lineTo(posX+5, 120);
    path2.lineTo(posX+15, 100);
    path2.lineTo(posX+5, 80);

    gNode.append("path")
      .attr("d", path2)
      .attr("class", "rline")
      .on("mousedown", (d) => this.onButton("right"))
      .on("mouseup", (d) => this.onEndButton())
  };
  // Creation de la molette
  createMolette = () => {
    const { svg, props: { nRay, displaytype } } = this;
    console.log("[D3MoletteComponent] Dessin avec nRay=" + nRay);

    const groupeReglage = svg.append("g").attr("id", "gMolette");
    this.drawMoletteWithAngleOn(groupeReglage, posX, 100, 35, 65, nRay);
    this.drawCenterPanelControlOn(groupeReglage);
    this.drawCircularControlOn(groupeReglage, posX, 100, 35, 65, displaytype);
  }
  // Mise à jour du dessin de la molette en fonction de la valeur de l'angle
  updateMolette = (a) => {
    // attention au passage a 360°
    let delta = (a - this.angle);
    let mdelta = delta;
    if ((360-Math.abs(mdelta)) <10) {
      console.log("[D3MoletteComponent] passage à 360 degrés");
      //valeur empirique pour indiquer qu'on vient d'en bas de la molette (rotation directe)
      mdelta=(this.angle>60) ? 360+mdelta:360-mdelta;
    }
    if (mdelta === 0) return;
    console.log("[D3MoletteComponent] rotation à " + a + " delta=" + mdelta);
    this.svg.select("#gMoletteRot").attr("transform", "rotate(" + a + ","+posX+",100)");
    const { props: { onDeltaAngle } } = this;
    onDeltaAngle(mdelta);
    this.angle = a;
  }

}

export default D3MoletteComponent;