import React from "react";
import { Redirect } from "react-router";
import {useApp} from "../../context/AppContext";
import Regul3 from "./Regul3";

/**
 * Stateless component for ACDS Notification.
 * Partie Pont vers le composant Regul3 avec Redirection si besoin selon la variable page
 * @name Notification2
 * @author Bruno Boissie <bruno.boissie@aviation-civile.gouv.fr>
 * @copyright (c) 2020, DSNA/DTI. All rights reserved.
 *
 * @param {Object}	props		Incoming component properties
 * @return	{Node}		Rendered Component node
 */
const Regul2 = props => {

	const [{reguls, page, user}, dispatch] = useApp();

	const navTo = page => {
		dispatch({
			type: "navTo",
			payload: {
				page
			}
		});
	};

	const navToPages = () => (user.email !== undefined && !page.includes("regulation/:"));

	console.log(`[Regul2] navToPages=${navToPages()}`);

	return (
		navToPages()
			? <Redirect to={page} />
			: <Regul3 />
			
	);
};

export default Regul2;
