import React, { useRef, useEffect } from "react";
import { Redirect } from "react-router";
import { useApp } from "../../context/AppContext";
import D3MoletteComponent from './D3MoletteComponent';

// la variable molette est située au dehors du composant pour pouvoir être utilisé dans le useEffect sur l'angle.
let molette = null;

/**
 * Stateless component for ACDS Notification.
 *
 * @name Date
 * @author Bruno Boissie <bruno.boissie@aviation-civile.gouv.fr>
 * @copyright (c) 2020, DSNA/DTI. All rights reserved.
 *
 * @param {Object}	props		Incoming component properties
 * @return	{Node}		Rendered Component node
 */
const ManageDate = props => {

  const [{ page, user, displaytype, histodate }, dispatch] = useApp();
  const { nRay } = props;

  const navTo = page => {
    dispatch({
      type: "navTo",
      payload: {
        page
      }
    });
  };


  const isHisto = () => (page === "history" || page === "/history");

  const navToPages = () => (user.email !== undefined && !isHisto());

  const refElement = useRef(null);

  const ye = new Intl.DateTimeFormat('fr', { year: 'numeric' }).format(histodate);
  const mo = new Intl.DateTimeFormat('fr', { month: 'short' }).format(histodate);
  const da = new Intl.DateTimeFormat('fr', { day: '2-digit' }).format(histodate);
  const wda = new Intl.DateTimeFormat('fr', { weekday: 'short' }).format(histodate);
  // const hour = new Intl.DateTimeFormat('fr', { hour: "2-digit" }).format(histodate).replace('h', '').replace(' ', '');
  // const minute = new Intl.DateTimeFormat('fr', { minute: "2-digit" }).format(histodate);
  // const secon = new Intl.DateTimeFormat('fr', { second: "2-digit" }).format(histodate);


  useEffect(() => {
    // Sur mount on regle l'heure de l'historique
    dispatch({
      type: "setHistoDate",
      payload: new Date()
    });
    // Le  handler a passer au sous composant D3
    const onDeltaAngle = (a) => {
      console.log("[ManageDate] Modification de la date :"+a);
      // 10 degre= un jour
      let dateOffset = a * 24 * 60 * 60 * 100;
      let myDate = histodate;
      myDate.setTime(myDate.getTime() + dateOffset);
      dispatch({
        type: "setHistoDate",
        payload: myDate
      });
    }

    //on associe le composant D3
    molette = new D3MoletteComponent(refElement.current, { nRay, displaytype, onDeltaAngle });

  }, []);

  return (
    navToPages()
      ? <Redirect to={page} />
      : (
        <div className="layerdate">
          <div className="date" id="date">
            <div className="datejour">
              {wda} {da} {mo}
            </div>
            <div className="datespace"/>
            <div ref={refElement} className="reglagejour" id="reglagejour" />
          </div>
        </div>
      )
  );
};

export default ManageDate;
