import React from "react";
import {useApp} from "../../context/AppContext";
/**
 * Stateless component for ACDS Notification.
 * Affichage détaillé d'une regulation
 * @name Regul3 
 * @author Bruno Boissie <bruno.boissie@aviation-civile.gouv.fr>
 * @copyright (c) 2020, DSNA/DTI. All rights reserved.
 *
 * @param {Object}	props		Incoming component properties
 * @return	{Node}		Rendered Component node
 */
const Regul3 = () => {

	const [{reguls, page}, ] = useApp();
	const idRegul = page.split("/")[2].replace(":", "");
	console.log(`[Regul3] idRegul=${idRegul}`);
	const regul = reguls[0].value.filter(r => r.regul_id === idRegul);
	const {hdep, harr, tv_id, regul_id, state, rate, delay, avdelay } = regul[0];

	return (
		<>
			<div className="regul2" id="regul2">
				<div className="line" id="line">
					<div className="col">
						{hdep}
						{" "}
						ID:
						{regul_id}
					</div>
					<div className="col">
						{harr}
						{" "}
						TV_ID:
						{tv_id}
					</div>
				</div>
				<div className="details">
					<div>
						Rate
						{" "}
						{rate}
					</div>
					<div>
						State
						{" "}
						{state}
					</div>
					<div>
						Delay
						{" "}
						{delay}
					</div>
					<div>
						Average Delay
						{" "}
						{avdelay}
					</div>
				</div>
			</div>

		</>
	);
};

export default Regul3;
