import React, {useMemo, useState} from "react";
import {useApp} from "../../context/AppContext";
/**
 * Stateless component for ACDS Agent.
 * Composant affiché dans la Page des Regulations
 * @name Regul
 * @author Bruno Boissie <bruno.boissie@aviation-civile.gouv.fr>
 * @copyright (c) 2020, DSNA/DTI. All rights reserved.
 *
 * @param {Object}	props		Incoming component properties
 * @return	{Node}		Rendered Component node
 */
const Regul = props => {
	const {regul_id, tv_id, hdep, harr} = props;

	const [,dispatch] = useApp();
	
	const [show, setShow] = useState(false);

	const navTo = page => {
		dispatch({
			type: "navTo",
			payload: {
				page
			}
		});
	};

	useMemo( () => {
		console.log(`[Regul] show=${show}`);
		if (show) navTo(`/regulation/:${regul_id}`);
	}, [show]);


	return (
		<>
			<div className="regul" id="regul" onClick={() => setShow(true)}>
				<div className="col">
					{hdep}
					{" "}
					ID:
					{regul_id}
				</div>
				<div className="col">
					{harr}
					{" "}
					TV_ID:
					{tv_id}
				</div>
			</div>
		</>
	);
};

export default Regul;
